.about-lectures {
  padding: 48px 0 64px;

  @include w-from($screen-lg) {
    padding: 60px 0 88px;
  }

  &__title {
    margin-bottom: 40px;

    @include w-from($screen-lg) {
      margin-bottom: 64px;
    }
  }

  &__accordeon {

  }

  &__item {
    border-radius: 4px;
    border: thin solid $color-dark-light;
    margin-bottom: 32px;
    transition-duration: $transition-hover-off;

    @include w-from($screen-lg) {
      margin-bottom: 40px;
    }

    &:last-child {
      margin-bottom: 0;
    }

    &:hover,
    &:focus {
      @include e-lvl-2();
      transition-duration: $transition-hover-on;
    }
  }

  &__label {
    @include display-flex(row, center);
    @include t-btn-link();
    height: 100px;
    padding-left: 96px;
    background-repeat: no-repeat;
    background-position: left center;
    background-size: 80px 100%;
    cursor: pointer;

    @include w-from($screen-md) {
      @include t-h4();
    }

    @include w-from($screen-lg) {
      @include t-h3();
      height: 160px;
      background-size: 175px 100%;
      padding-left: 191px;
    }

    > svg {
      margin-left: auto;
      margin-right: 16px;
      width: 32px;
      height: 32px;
      flex-shrink: 0;
      fill: $color-dark-light;
      transition-duration: 500ms;

      @include w-from($screen-lg) {
        width: 64px;
        height: 64px;
      }
    }

    &.mod-washing {
      background-image: url("../assets/img/about-lectures/img-1--mobile.png");

      @include w-from($screen-lg) {
        background-image: url("../assets/img/about-lectures/img-1.png");
      }
    }

    &.mod-conditioner {
      background-image: url("../assets/img/about-lectures/img-2--mobile.png");

      @include w-from($screen-lg) {
        background-image: url("../assets/img/about-lectures/img-2.png");
      }
    }

    &.mod-open {
      svg {
        transform: rotate(-180deg);
        transition-duration: 500ms;
      }
    }
  }

  &__content {
    border-top: thin solid $color-dark-light;
    padding: 24px 12px 32px;
    display: none;

    @include w-from($screen-md) {
      padding: 24px 24px 32px;
    }
    @include w-from($screen-md) {
      padding: 32px 40px 64px;
    }
  }

  &__inner {
    h4 {
      margin-bottom: 24px;
    }

    &.mod-grid {
      > div {
        margin-bottom: 48px;
      }
    }

    &.mod-flex {
      margin-bottom: 40px;

      @include w-to($screen-md) {
        > div + div {
          padding-top: 32px;
        }
      }
    }

    &.mod-features {
      .t-tag,
      li {
        margin-bottom: 12px;
      }

      li:last-child {
        margin-bottom: 0;
      }
    }

    @include w-from($screen-md) {
      &.mod-flex {
        @include display-flex();
        margin-bottom: 48px;
        > div:first-child {
          margin-right: 24px;
        }
      }
    }

    @include w-from($screen-lg) {
      &.mod-grid {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 48px;
        margin-bottom: 32px;
      }

      &.mod-flex {
        > div:first-child {
          margin-right: 48px;
        }
      }
    }

    @include w-from($screen-xl) {
      &.mod-grid {
        grid-gap: 40px;
      }
    }
  }

  &__notification {
    margin-bottom: 48px;
  }

  &__whom {
    display: grid;
    grid-gap: 32px;

    h4 {
      margin-bottom: 12px;
    }

    @include w-from($screen-md) {
      grid-template-columns: 1fr 1fr;
      grid-column-gap: 16px;
      grid-row-gap: 32px;

      > h3 {
        grid-column: 1 / 3;
      }
    }

    @include w-from($screen-lg) {
      grid-template-columns: 1fr 1fr 1fr;
      grid-column-gap: 24px;

      > h3 {
        grid-column: 1 / 4;
      }

      h4 {
        margin-bottom: 16px;
      }
    }
  }
}
