.features {
  padding: 32px 0;

  @include w-from($screen-md) {
    padding: 36px 0 48px;
  }
  @include w-from($screen-lg) {
    padding: 48px;
  }

  &__list {
    @include w-from($screen-md) {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-column-gap: 32px;
      grid-row-gap: 48px;
    }

    @include w-from($screen-lg) {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }

  &__item {
    text-align: center;
    margin-bottom: 32px;
    svg {
      display: block;
      margin: 0 auto 8px;
    }

    @include w-from($screen-md) {
      margin-bottom: 0;
    }
  }
}
