.authors {
  padding: 48px 0  64px;

  @include w-from($screen-md) {
    padding: 48px 0 120px;
  }
  @include w-from($screen-lg) {
    padding: 64px 0 120px;
  }

  &__title {
    margin-bottom: 48px;

    @include w-from($screen-lg) {
      margin-bottom: 30px;
    }
    @include w-from($screen-xl) {
      margin-bottom: 64px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 40px;

    @include w-from($screen-lg) {
      grid-template-columns: 1fr 1fr;
      grid-gap: 60px;
    }

    @include w-from($screen-xl) {
      grid-gap: 80px;
    }
  }

  &__item {
    display: grid;
    grid-template-columns: 80px auto;
    grid-gap: 24px;
    width: 100%;
    max-width: 450px;
    margin: 0 auto;

    img {
      width: 80px;
      height: 80px;
      display: block;
      object-fit: cover;
    }

    @include w-from($screen-md) {
      grid-template-columns: 132px auto;

      img {
        width: 132px;
        height: 132px;
      }
    }

    @include w-from($screen-lg) {
      &:first-child {
        margin-right: 0;
      }
      &:last-child {
        margin-left: 0;
      }
    }
  }

  &__info {
    @include display-flex(column, stretch, center);
  }

  &__name {
    @include t-h4();
    margin-bottom: 12px;
  }
}
