.faq {
  padding: 48px 0 64px;
  background-color: $color-bg;

  @include w-from($screen-md) {
    padding: 64px 0;
  }

  &__title {
    margin-bottom: 32px;

    @include w-from($screen-md) {
      margin-bottom: 64px;
    }
  }

  &__item {
    border: thin solid $color-dark-light;
    padding: 10px 16px;
    border-radius: 4px;
    margin-bottom: 16px;
    transition-duration: $transition-hover-off;

    &:hover {
      @include e-lvl-2();
      background-color: white;
      transition-duration: $transition-hover-on;
    }

    @include w-from($screen-xl) {
      padding: 10px 32px;
      margin-bottom: 24px;
    }
  }

  &__label {
    @include t-btn-link();
    @include display-flex(row, center, space-between);
    box-sizing: border-box;
    position: relative;
    width: 100%;
    padding-right: 32px;
    cursor: pointer;

    svg {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      width: 32px;
      height: 32px;
      margin-left: 32px;
      transition-duration: $transition-hover-off;

      @include w-to($screen-md) {
        top: -5px;
      }

      @include w-from($screen-xl) {
        top: -10px;
      }
    }

    &.mod-open {
      svg {
        transform: rotate(-180deg);
        transition-duration: $transition-hover-on;
      }
    }

    @include w-from($screen-md) {
      @include t-h4();
    }

    @include w-from($screen-xl) {
      padding-right: 48px;
      svg {
        width: 48px;
        height: 48px;
        right: 0;
      }
    }
  }

  &__content {
    padding-top: 17px;
    padding-bottom: 12px;
    display: none;
    a.faq__content-btn {
      @include t-default();
      margin-top: 24px;
    }

    @include w-from($screen-md) {
      @include t-default-big();
      a {
        @include t-default-big();
      }
      a.faq__content-btn {
        @include t-default();
      }
    }
  }
}
