.feedback {
  padding: 48px 0;
  background-color: $color-bg;

  @include w-from($screen-md) {
    padding: 64px 0 48px;
  }

  @include w-from($screen-md) {
    padding: 64px 0 64px;
  }

  &__title {
    margin-bottom: 32px;

    @include w-from($screen-md) {
      margin-bottom: 48px;
    };

    @include w-from($screen-lg) {
      margin-bottom: 64px;
    };
  }

  &__slider {
    @include display-flex(row, stretch);
    width: 100%;
    padding-bottom: 36px;
    overflow: auto;

    @include w-to($screen-lg) {
      max-width: 343px;
      margin-left: auto;
      margin-right: auto;
    }
    @include w-from($screen-lg) {
      overflow-x: scroll;
      overflow-y: hidden;
    }
  }

  &__slide {
    position: relative;
    box-sizing: border-box;
    padding: 12px;
    flex-shrink: 0;

    @include w-from($screen-lg) {
      width: 343px;
    }

    > div {
      @include aspect-ratio(343, 470);
      display: block;
    }

    img {
      position: absolute;
      left: 0;
      top: 0;
      display: block;
      width: 100%;
      height: 100%;
    }
  }

  .slick-dots {
    @include display-flex(row, center, center);
    position: absolute;
    top: calc(100% - 36px);
    width: 100%;

    button {
      color: transparent;
      position: absolute;
      display: block;
      width: 11px;
      height: 11px;
      border-radius: 50%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      &::after {
        content: '';
        display: block;
        position: absolute;
        width: 22px;
        height: 22px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }

    li {
      position: relative;
      display: block;
      width: 16px;
      height: 16px;
      margin: 6px;
      border-radius: 50%;
      border: thin solid $color-dark-light;

      &.slick-active {
        button {
          background-color: $color-dark-light;
        }
      }
    }

  }
}
