.head-section {
  margin-bottom: 48px;

  @include w-from($screen-lg) {
    margin-bottom: 98px;
  }

  @include w-from($screen-xl) {
    margin-bottom: 60px;
  }

  &__wrapper {
    position: relative;

    @include w-from($screen-lg) {
      &::before {
        padding-top: (440 / 1024) * 100%;
      }
    }
    @include w-from($screen-xl) {
      &::before {
        padding-top: (577 / 1440) * 100%;
      }
    }
  }

  &__banner {
    @include aspect-ratio(375, 200);

    @include w-from($screen-md) {
      &::before {
        padding-top: (281 / 667) * 100%;
      }
    }

    @include w-from($screen-lg) {
      &::before {
        display: none;
      }
      a {
        position: relative;
      }
    }

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__content {
    padding-top: 32px;
    padding-bottom: 56px;

    @include w-from($screen-lg) {
      @include display-flex(column, stretch, center);
      position: absolute;
      left: 50%;
      top: 0;
      height: 100%;
      width: 100%;
      padding-top: 6px;
      padding-bottom: 50px;
      transform: translateX(-50%);
    }
  }

  &__title {
    @include t-h2();
    margin-bottom: 32px;

    @include w-from($screen-lg) {
      margin-bottom: 48px;
    }
  }

  &__advantages {
    li {
      margin-bottom: 16px;

      @include w-from($screen-lg) {
        @include t-h4();
      }
    }
  }
}
