[data-popup=question-send] {
  .popup__close {
    display: none;
  }

  .popup__container {
    padding: 32px 16px;

    @include w-from($screen-lg) {
      padding: 32px;
    }
  }
}

.question-send {
  h3 {
    @include t-h4();
    margin-bottom: 16px;
    text-align: center;

    @include w-from($screen-lg) {
      @include t-h3();
    }
  }

  p {
    margin-bottom: 40px;
    text-align: center;
  }

  &__button {
    width: 100%;
  }
}
