.price-item {
  @include display-flex(column, stretch);
  box-sizing: border-box;
  min-height: 560px;
  background-color: white;
  border-radius: 4px;
  border: thin solid $color-dark-light;

  &__head {
    @include display-flex(column, stretch, space-between);
    box-sizing: border-box;
    height: 157px;
    padding: 24px 24px 16px;
    border-bottom: thin solid $color-dark-light;
    flex-shrink: 0;

    &-bottom {
      @include display-flex(row, flex-end, space-between);
    }
  }

  &__price {
    span {
      display: block;
      text-align: right;
    }

    .mod-old {
      text-decoration: line-through;
      color: $color-dark-light;
    }

    .mod-new {
      @include t-h4();
    }
  }

  &__content {
    @include display-flex(column, stretch);
    box-sizing: border-box;
    padding: 16px 24px 32px;
    flex-grow: 1;

    > p {
      padding-bottom: 24px;
    }
  }

  &__subtitle {
    @include t-caption-upper();
    padding-top: 24px;
    padding-bottom: 12px;
  }

  &__btn-container {
    margin-top: auto;
    margin-bottom: 0;
  }

  &__button {
    width: 100%;
    margin-top: 40px;
  }
}
