.footer {
  padding-top: 48px;
  padding-bottom: 24px;
  margin-top: auto;
  margin-bottom: 0;
  color: $color-dark;
  background-color: $color-bg;

  @include w-from($screen-md) {
    padding-bottom: 32px;
  }
  @include w-from($screen-lg) {
    padding-top: 40px;
  }

  &__top {
    @include w-from($screen-lg) {
      @include display-flex(row-reverse);
    }
  }

  &__bottom {
    @include display-flex(row, center, space-between);
    padding-top: 16px;
    flex-wrap: wrap;
    border-top: thin solid $color-dark-15;
  }

  &__menu {
    flex-grow: 1;
  }

  &__subscribe {
    margin-bottom: 16px;
    text-align: center;

    @include w-from($screen-md) {
      margin-bottom: 48px;
    }

    @include w-from($screen-lg) {
      width: 258px;
      flex-shrink: 0;
      margin-left: 24px;
    }

    @include w-from($screen-xl) {
      margin-left: 48px;
    }

    p {
      @include font-size(16px, 0.2, 22px);
      max-width: 320px;
      margin: 0 auto 24px;
      font-weight: 500;
      text-align: center;

      @include w-from($screen-md) {
        margin-bottom: 16px;
      }
    }

    .button {
      width: 100%;
      max-width: 375px;
      margin: 0 auto;

      @include w-from($screen-md) {
        max-width: 320px;
      }
    }
  }

  &__logo {
    padding-bottom: 8px;
    flex-shrink: 0;
    a {
      @include display-flex(row, center, center);
      @include font-size(18px, 0.2px, 22px);
      width: 100%;
      text-decoration: none;
      font-weight: bold;
      color: $color-dark;

      img {
        display: block;
        margin-right: 12px;
      }
    }

    @include w-from($screen-md) {
      padding-bottom: 0;
    }
  }

  &__social {
    @include display-flex(row, center);
    padding-bottom: 8px;

    @include w-from($screen-md) {
      padding-bottom: 0;
      margin-right: auto;
      margin-left: 48px;
    }
  }

  &__social-link {
    display: inline-block;
    line-height: 0;

    svg {
      fill: $color-dark;
    }

    + .footer__social-link {
      margin-left: 16px;
    }
  }

  &__contacts {
    flex-basis: 100%;
    flex-grow: 1;
    text-align: center;

    a {
      font-weight: normal;
      color: $color-dark;
      &:hover,
      &:focus {
        color: $color-link-color;
      }
    }

    @include w-from($screen-md) {
      flex-basis: unset;
      flex-grow: 0;
    }
  }
}
