.form {
  &__field {
    box-sizing: border-box;
    width: 100%;
    padding-bottom: 16px;
  }

  &__button-container {
    position: relative;
  }

  &__alert {
    display: flex;
    flex-direction: row;
    align-items: center;
    position: absolute;
    bottom: calc(100% + 16px);
    color: $color-error;

    svg {
      display: block;
      margin-right: 16px;
    }
  }
}
