.header {
  position: sticky;
  top: -56px;
  background-color: white;
  z-index: 200;

  @include e-bottom-divider();

  @include w-from($screen-md) {
    top: 0;
  }

  &__wrapper {
    @include w-from($screen-md) {
      @include display-flex(row, center);
    }
  }

  &__logo {
    padding: 16px 0 0;
    a {
      @include display-flex(row, center, center);
      @include font-size(18px, 0.2px, 22px);
      width: 100%;
      text-decoration: none;
      font-weight: bold;
      color: $color-dark;

      img {
        display: block;
        margin-right: 12px;
      }
    }

    @include w-from($screen-md) {
      padding: 0 96px 0 0;
    }
  }

  &__nav {
    overflow: hidden;

    &-list {
      @include display-flex(row, center, center);
      flex-wrap: wrap;
      margin: 0 -24px;

      @include w-from($screen-md) {
        margin: 0 -24px;
      }
    }

    &-icon {
      display: none;
    }

    &-item {
      a {
        position: relative;
        display: inline-flex;
        align-items: center;
        padding: 20px 24px 16px;
        color: $color-dark;

        svg {
          transition-duration: $transition-hover-off;
        }

        &::before {
          content: '';
          display: inline-block;
          position: absolute;
          width: calc(100% - 48px);
          bottom: 0;
          left: 24px;
          height: 2px;
          background-color: $color-dark;
          transform: scaleX(0);
          pointer-events: none;
          transition: transform $transition-slow;
          transform-origin: left;
        }

        @include w-from($screen-md) {
          padding: 30px 24px 28px;
        }
      }

      &.mod-drop {
        a > svg {
          display: inline-block;
        }
      }

      &.mod-open {
        a > svg {
          transform: rotate(-180deg);
          transition-duration: $transition-hover-on;
        }
      }

      a {
        &.mod-hover,
        &.mod-open {
          position: relative;
          &::before {
            transform: scaleX(1);
            transition: transform $transition-medium;
          }
        }
      }

      @include w-from($screen-lg) {
        a {
          &:hover,
          &.mod-open {
            position: relative;
            &::before {
              transform: scaleX(1);
              transition: transform $transition-medium;
            }
          }
        }
      }

    }
  }

  &__dropdown {
    @include e-lvl-2();
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background-color: white;
    border-top: thin solid $color-dark-15;
    display: none;
  }

  &__dropdown-wrapper {
    box-sizing: border-box;
    padding-top: 8px;
    padding-bottom: 8px;

    @include w-from($screen-md) {
      @include display-flex(row, stretch, center);
      padding-top: 24px;
      padding-bottom: 24px;
    }
  }

  &__dropdown-link {
    display: grid;
    grid-template-columns: 92px auto;
    grid-gap: 16px;
    padding: 16px 0;
    height: 64px;
    color: $color-dark;

    &:hover,
    &:focus,
    &.mod-hover {
      color: $color-link-color;
    }


    > div {
      @include display-flex(column, stretch, space-between);
      height: 100%;
    }

    p {
      @include t-btn-link();
      color: inherit;
    }

    span {
      @include t-caption();
      color: $color-dark;
    }

    &:not(:last-child) {
      border-bottom: thin solid $color-dark-15;
    }

    @include w-from($screen-md) {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      padding: 0 16px;
      width: 220px;
      height: auto;
      flex-shrink: 0;

      img {
        display: block;
        margin-bottom: 8px;
      }

      p {
        margin-bottom: 6px;
        text-align: center;
      }
      span {
        text-align: center;
      }

      > div {
        justify-content: flex-start;
        height: auto;
      }

      &:not(:last-child) {
        border: none;
      }
    }
  }
}
