.promo-banner {
  background-color: $color-dark;

  &__container {
    padding-top: 24px;
    padding-bottom: 24px;
    text-align: center;

    @include w-from($screen-lg) {
      @include display-flex(row, center, space-between);
    }
  }

  &__text {
    @include t-h4();
    margin-bottom: 24px;
    text-align: center;
    color: white;

    @include w-from($screen-lg) {
      margin-bottom: unset;
    }
  }

  &__button {

  }
}
