.input {
  position: relative;
  display: block;

  &__placeholder {
    @include t-default();
    position: absolute;
    display: inline-block;
    padding: 0 6px;
    top: 50%;
    left: 16px;
    background-color: white;
    color: $color-dark-light;
    transition-duration: $transition-hover-off;
    transform: translateY(-50%);
    z-index: 2;
    pointer-events: none;
  }

  &__footer-info {
    @include t-caption();
    position: absolute;
    padding-top: 4px;
    color: $color-dark-light;
    display: none;
  }

  &__reset {
    position: absolute;
    display: inline-block;
    top: 50%;
    right: 7px;
    border: none;
    background: transparent;
    box-shadow: none;
    transform: translateY(-50%);
    pointer-events: none;
    opacity: 0;
    z-index: 2;
    transition-duration: $transition-hover-off;

    &::after {
      content: '';
      display: block;
      position: absolute;
      width: 24px;
      height: 24px;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      background-color: rgba(white, 0.85);
      border-radius: 4px;
      z-index: -1;
    }

    svg {
      display: block;
      fill: $color-dark-light;
    }
  }

  &.mod-active {
    .input__reset {
      opacity: 1;
      pointer-events: auto;
      transition-duration: $transition-hover-on;
    }

    .input__placeholder {
      @include t-caption();
      top: 0;
      color: $color-dark;
      transform: translate(-6px, -50%);
      transition-duration: $transition-hover-on;
    }
  }

  &.mod-error {
    .input-text {
      border-color: $color-error;
    }
    .input__footer-info {
      color: $color-error;
    }

    .input__placeholder {
      color: $color-error;
    }
  }

  &.mod-info {
    margin-bottom: 20px;

    .input__footer-info {
      display: block;
    }
  }
}

.input-text {
  @include t-default();
  position: relative;
  box-sizing: border-box;
  width: 100%;
  padding: 13px 24px 13px 16px;
  border-radius: 4px;
  border: thin solid $color-dark-30;
  color: $color-dark;
  transition-duration: $transition-hover-off;

  &::placeholder {
    display: none;
  }

  &:focus,
  &:hover {
    border-color: black;
    transition-duration: $transition-hover-on;
  }

  &:focus {
    + .input__placeholder {
      @include t-caption();
      top: 0;
      color: $color-dark;
      transform: translate(-6px, -50%);
      transition-duration: $transition-hover-on;
    }
  }
}
