[data-popup=cookie] {
  &.popup {
    background: transparent;
    backdrop-filter: none;
    pointer-events: none;

    &.mod-show {

      .popup__container {
        animation-name: cookieShow;
      }
    }
  }

  .popup__close {
    @include w-from($screen-md) {
      top: 8px;
      right: 8px;
      left: unset;
      background-color: transparent;

      svg {
        fill: $color-dark-30;
      }

      &:hover {
        svg {
          fill: $color-dark;
          transition-duration: $transition-hover-on;
        }
      }
    }
  }

  .popup__container {
    padding: 16px;
    max-width: 343px;
    top:              unset;
    left:             16px;
    bottom: 16px;
    transform: none;
    pointer-events: auto;

    @include w-from($screen-lg) {
      padding: 32px;
    }
  }
}

.popup-cookie {
  text-align: center;
  h2 {
    @include t-btn-link();
    margin-bottom: 12px;
    text-align: center;
  }

  p {
    @include t-caption();
    margin-bottom: 16px;
    text-align: center;

    a {
      @include t-caption();
      color: $color-dark;
      text-decoration: underline;

      &:hover,
      &:focus {
        color: $color-link-color;
      }
    }
  }

  &__button {
    width: 150px;
    margin-left: auto;
    margin-right: auto;
  }
}
