.footer-menu {
  margin-bottom: 48px;

  @include w-from($screen-md) {
    margin-bottom: 32px;
  }


  &__list {
    @include w-from($screen-md) {
      @include display-flex(row, stretch);
    }
  }

  &__item {
    padding-top: 32px;

    @include w-from($screen-md) {
      flex-basis: 33%;
      flex-grow: 1;
      padding-top: 0;

      + .footer-menu__item {
        margin-left: 24px;
      }
    }
  }

  &__label {
    display: inline-block;
    margin-bottom: 16px;
    color: $color-dark-light;
    font-weight: 500;

    @include w-from($screen-md) {
      margin-bottom: 24px;
    }
  }

  &__sublist {
    margin: -6px 0;
  }

  &__subitem {
    margin: 6px 0;

    a {
      color: $color-dark;

      &:hover,
      &:focus,
      &.mod-hover {
        color: $color-link-color;
      }
    }
  }
}
