.question-form {
  padding: 64px 0 120px;

  &__form {
    @include e-lvl-3();
    box-sizing: border-box;
    max-width: 520px;
    padding: 32px 16px 35px;
    margin: 0 auto;
    border-radius: 4px;
    background-color: white;
    border: thin solid $color-dark-light;

    @include w-from($screen-md) {
      padding: 32px 32px 48px;
    }
  }

  &__title {
    text-align: center;
    margin-bottom: 32px;
  }

  &__button {
    display: block;
    width: 100%;
    margin-top: 32px;
    margin-bottom: 16px;
  }

  &__policy {
    @include t-caption();
    color: $color-dark-light;

    a {
      @include t-caption();
      color: $color-dark-light;
      text-decoration: underline;

      &:hover,
      &:focus,
      &.mod-hover {
        color: $color-link-color;
      }
    }
  }
}
