[data-popup=video] {
  .popup__container {
    max-width: 960px;
    padding: 0;
    border-radius: 0;
  }

  .popup__close {
    @include w-to($screen-lg) {
      top: unset;
      bottom: calc(100% + 8px);
      right: 0;
      left: unset;
      background-color: $color-dark-15;

      svg {
        fill: $color-dark;
      }

      &:hover {
        svg {
          fill: white;
          transition-duration: $transition-hover-on;
        }
      }
    }
  }
}

.popup-video {
  @include aspect-ratio(960, 540);

  iframe {
    width: 100%;
    height: 100%;
  }
}
