.format-lectures {
  padding: 64px 0;

  &__title {
    margin-bottom: 64px;
  }

  &__list {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 48px;

    @include w-from($screen-lg) {
      grid-template-columns: 1fr 1fr 1fr;
      grid-gap: 32px;
    }
  }

  &__item {
    text-align: center;

    img {
      display: inline-block;
      margin-bottom: 24px;
    }

    p {
      @include t-default();
      max-width: 375px;
      margin: 0 auto;
      text-align: center;
    }
  }
}
