.textarea {
  $this: &;

  position: relative;
  display: block;
  margin-bottom: 24px;

  &__placeholder {
    @include t-default();
    position: absolute;
    display: inline-block;
    padding: 0 6px;
    top: 12px;
    left: 16px;
    background-color: white;
    color: $color-dark-light;
    transition-duration: $transition-hover-off;
    //transform: translateY(-50%);
    z-index: 2;
    pointer-events: none;
  }

  &__footer-info {
    @include t-caption();
    width: 100%;
    position: absolute;
    padding: 4px 0;
    text-align: right;
    color: $color-dark-light;
    display: none;
  }

  &__reset {
     position: absolute;
     display: inline-block;
     top: 12px;
     right: 7px;
     border: none;
     background: transparent;
     box-shadow: none;
     pointer-events: none;
     opacity: 0;
     z-index: 2;
     transition-duration: $transition-hover-off;

    &::after {
      content: '';
      display: block;
      position: absolute;
      width: 24px;
      height: 24px;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      background-color: rgba(white, 0.85);
      border-radius: 4px;
      z-index: -1;
    }

     svg {
       display: block;
       fill: $color-dark-light;
     }
   }

  &__element {
    @include t-default();
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 134px;
    padding: 13px 24px 13px 16px;
    border-radius: 4px;
    border: thin solid $color-dark-30;
    color: $color-dark;
    transition-duration: $transition-hover-off;
    resize: none;

    &::placeholder {
      display: none;
    }

    &:focus,
    &:hover {
      border-color: black;
      transition-duration: $transition-hover-on;
    }

    &:focus {
      + #{$this}__placeholder {
        @include t-caption();
        top: 0;
        color: $color-dark;
        transform: translate(-6px, -50%);
        transition-duration: $transition-hover-on;
      }
    }
  }

  &.mod-active {
    #{$this}__reset {
      opacity: 1;
      pointer-events: auto;
      transition-duration: $transition-hover-on;
    }

    #{$this}__placeholder {
      @include t-caption();
      top: 0;
      color: $color-dark;
      transform: translate(-6px, -50%);
      transition-duration: $transition-hover-on;
    }
  }

  &.mod-error {
    #{$this}__element {
      border-color: $color-error;
    }
    #{$this}__footer-info {
      color: $color-error;
    }

    #{$this}__placeholder {
      color: $color-error;
    }
  }

  &.mod-info {
    margin-bottom: 20px;

    #{$this}__footer-info {
      display: block;
    }
  }
}

